<template>
	<div id="box">
		<div id="nav">
			<span @click="submit(1)" :class="{active:index1==1}">未使用</span>
			<span @click="submit(2)" :class="{active:index1==2}">已使用</span>
			<span @click="submit(3)" :class="{active:index1==3}">已过期</span>
		</div>
		<div id="quan" v-for="(item,index) in data" :key="index" :class="{guoqi:index1!=1}">
			<div class="item">
				<div class="card">
					<div class="inner left">
						<h1>￥{{item.money}}</h1>
						<span>满{{item.condition}}可用</span>
					</div>
					<div class="inner middle" style="width:30%;">
						<h2 class="fs-5-1">{{item.name}}</h2>
						<p>{{item.use_scope}}</p>
					</div>
					<div class="btn">{{index1==1?"去使用":index1==2?"已使用":"未使用"}}</div>
				</div>
				<span class="shijian">有效期至[{{item.send_time}}]</span>
			</div>
		</div>
		<div v-if="data.length==0" class="nomore"> ~ 暂无数据 ~ </div>
	</div>	
</template>
<script>
import {Button} from "vant";
import { youhuiquan_list } from '@/axios/getData';
export default {
	data(){
		return{
			index1:1,
			data:[],
			page:1
		}
	},
	components:{
		"van-button":Button
	},
	created(){
		this.submit(1);
	},
	methods:{
		async submit(id){
			this.page=1;
			switch(id){
				case 1:
					var res = await youhuiquan_list({type:0,p:this.page})
					this.data=res.data;
					this.index1=1;
					console.log(this.data)
					break;
				case 2:
					this.index1=2;
					this.data=[];
					var res = await youhuiquan_list({type:1,p:this.page})
					this.data=res.data;
					console.log(this.data)
					break;
				case 3:
					this.index1=3;
					this.data=[];
					
					var res = await youhuiquan_list({type:2,p:this.page})
					this.data=res.data;
					break;
				default:
			}
		}
	}
}
</script>
<style lang="scss" scoped>
@media screen and (max-width: 320px) {
	.fs-5-1{font-size: 12pt !important;}
	.wd5{width:30% !important;}
}
	#box{
		// height:100vh;
		background: #FAFAFA;
		#nav{
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			margin: 50px 0;
			background: #fff;
			padding-bottom:20pt;
			span{
				color:#000; 
			}
			.active{
				position: relative;
				color:#15BDDB;
				font-weight: 600;
			}
			.active::after{
    			position: absolute;
    			content: "";
    			width: 70%;
    			height: 0.066667rem;
    			background: #15BDDB;
    			left: 15%;
    			bottom: -20px;
			}
		}
		#quan{
			// border-bottom: 1px solid #ccc;
			padding:10pt;
			.item{
				background: #fff;
				border-radius: 10pt;
				// border: 2pt solid #eee;
  			box-shadow:0px 0px 6px rgba(167, 165, 165, 0.5);
			}
			.shijian{
				display: inline-block;
				padding-top:5pt;
				width:90%;
				margin:42px;
				color: #A9A9A9;
				border-top: 1pt dashed #eee;
			}
			.card{
				display: flex;
				background: #fff;
				flex-direction: row;
				align-items: center;
				justify-content: space-around;
				border-radius: 10pt;
				.btn{
					background: #15BDDB;
					color:#fff;
					padding:3pt 10pt;
					border-radius: 15pt;
					margin-top: 15px;
				}
				.inner{
					display: flex;
					flex-direction: column;
					h1{
						color:red;
					}
				}
				.left{
					padding-top: 15px;
					align-items: center;
					color:#A9A9A9;
				}
				.middle{
					// align-items: right;
					padding-top: 15px;
					display: flex;
					flex-direction: column;
					span{
						margin: 10px 0;
						color: #A9A9A9;
					}
					p{
						display: inline-block;
						margin-top:10px;
					}
				}
			}
		}
		.guoqi{
			color: #A9A9A9 !important;
			h1{
				color: #A9A9A9 !important;
			}
			button{
				background: #A9A9A9 !important;
				border:none;
			}
		}
	}
	.nomore{
		text-align: center;
		color:#999;
		font-size: 13pt;
	}
</style>


